* {
  border: none;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

body {
  background-color: #e4f7a8;
}

.event-container {
  background-image: url("Keroppi-background.4423f449.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 98%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 600px;
  display: flex;
}

.event-container-title {
  color: #8d9015;
  font-family: RocknRoll One, sans-serif;
  font-size: 60px;
  font-weight: 500;
}

.event-container-subtitle {
  color: #9da02b;
  margin-top: 24px;
  font-size: 24px;
  font-weight: 400;
}

.event-container-time {
  color: #f0f4a3;
  background-color: #b4b724;
  border-radius: 15px;
  flex-direction: row;
  margin: 52px 0;
  padding: 8px;
  font-size: 48px;
  font-weight: 300;
  display: flex;
}

.event-container-time-p {
  margin-left: 9px;
  margin-right: 9px;
}

.divisor {
  border-bottom: 10px dotted #b4b724;
  width: 90%;
  height: 1px;
}

.divisor-container {
  justify-content: center;
  display: flex;
}

.pix {
  justify-content: space-around;
  margin: 60px 0;
  display: flex;
}

.pix-container {
  justify-content: space-around;
  width: 90%;
  display: flex;
}

.pix-container-img {
  width: 320px;
}

.pix-img {
  width: 100%;
}

.pix-text {
  background-color: #b4b724;
  border-radius: 15px;
  flex-direction: column;
  place-content: center;
  align-items: center;
  width: 40%;
  margin-top: 105px;
  padding: 25px;
  display: flex;
}

.pix-text-paragraph {
  color: #f0f4a3;
  flex-direction: column;
  justify-content: center;
  width: 90%;
  display: flex;
}

.pix-text-title {
  color: #f0f4a3;
  margin-bottom: 25px;
  font-family: RocknRoll One, sans-serif;
  font-size: 48px;
  font-weight: 400;
}

.paragraph {
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 500;
}

@media (width <= 1200px) {
  .pix-container {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }

  .pix-container-img {
    width: 60%;
  }

  .pix-text {
    width: 60%;
    margin-top: 25px;
    margin-bottom: 50px;
  }

  .pix-text-title {
    font-size: 64px;
  }

  .paragraph {
    font-size: 22px;
  }

  .event-container {
    background-position: bottom;
    background-size: 90%;
  }
}

@media (width <= 750px) {
  .event-container {
    height: auto;
  }

  .event-container-title {
    text-align: center;
    margin: 32px 8px 0;
    font-size: 50px;
  }

  .event-container-subtitle {
    text-align: center;
    font-size: 18px;
  }

  .event-container-time {
    margin-top: 24px;
    margin-bottom: 80px;
    font-size: 24px;
  }

  .pix-container-img {
    width: 85%;
  }

  .pix-text {
    width: 80%;
  }

  .pix-text-title {
    font-size: 42px;
  }

  .pix .paragraph {
    font-size: 16px;
  }
}
